/**
 * BullMQ Queue Configuration for Health Checks and Background Jobs
 */

import { Queue, Worker, QueueEvents } from 'bullmq'
import { Redis } from 'ioredis'
import { db } from './db'
import { HealthCheckerService } from './healthChecker'
import { emailService } from './emailService'
import { analyticsService } from './analyticsService'

// Redis connection configuration
let redis: Redis | null = null

try {
  // Debug environment variables
  console.log('Checking Redis configuration:', {
    redisHost: process.env.REDIS_HOST,
    redisPort: process.env.REDIS_PORT,
    hasRedisPassword: !!process.env.REDIS_PASSWORD,
    allEnvVars: Object.keys(process.env).filter(key => key.startsWith('REDIS'))
  })

  // Try REDIS_URL first (full connection string), then fall back to individual variables
  if (process.env.REDIS_URL) {
    console.log('Attempting Redis connection using REDIS_URL:', process.env.REDIS_URL.replace(/\/\/default:[^@]+@/, '//default:***@'))
    redis = new Redis(process.env.REDIS_URL, {
      maxRetriesPerRequest: null, // Required by BullMQ for blocking operations
      connectTimeout: 5000, // 5 second timeout
      lazyConnect: true, // Don't connect immediately
      db: 0, // Force database 0 (default)
    })
  } else if (process.env.REDIS_HOST && process.env.REDIS_PASSWORD) {
    console.log('Attempting Redis connection to:', process.env.REDIS_HOST)
    redis = new Redis({
      host: process.env.REDIS_HOST,
      port: parseInt(process.env.REDIS_PORT || '6379'),
      password: process.env.REDIS_PASSWORD,
      maxRetriesPerRequest: null, // Required by BullMQ for blocking operations
      connectTimeout: 5000, // 5 second timeout
      lazyConnect: true, // Don't connect immediately
      db: 0, // Force database 0 (default)
    })
  } else {
    console.log('Redis not configured - skipping connection')
  }
  
  if (redis) {
    // Test the connection
    redis.connect().then(() => {
      console.log('✅ Redis connected successfully')
    }).catch((error) => {
      console.error('❌ Redis connection failed:', error.message)
      redis = null
    })
  }
} catch (error) {
  console.error('Failed to initialize Redis connection:', error)
  redis = null
}

// Create separate Redis connection instances for each BullMQ component
// This ensures each component uses database 0 explicitly
let bullmqQueueConnection: Redis | null = null
let bullmqWorkerConnection: Redis | null = null
let bullmqEventsConnection: Redis | null = null

if (redis) {
  try {
    // Create Redis connection configuration
    let connectionConfig: any = null
    
    if (process.env.REDIS_URL) {
      // Clean the REDIS_URL (remove any trailing quotes)
      const cleanRedisUrl = process.env.REDIS_URL.replace(/['"]+$/, '')
      console.log('Cleaned REDIS_URL:', cleanRedisUrl.replace(/\/\/default:[^@]+@/, '//default:***@'))
      
      // Parse REDIS_URL to extract connection details
      const url = new URL(cleanRedisUrl)
      connectionConfig = {
        host: url.hostname,
        port: parseInt(url.port) || 6379,
        password: url.password,
        db: 0, // Explicitly force database 0
        maxRetriesPerRequest: null,
        retryDelayOnFailover: 100,
        connectTimeout: 5000,
        lazyConnect: true,
        enableAutoPipelining: false,
      }
    } else if (process.env.REDIS_HOST && process.env.REDIS_PASSWORD) {
      connectionConfig = {
        host: process.env.REDIS_HOST,
        port: parseInt(process.env.REDIS_PORT || '6379'),
        password: process.env.REDIS_PASSWORD,
        db: 0, // Explicitly force database 0
        maxRetriesPerRequest: null,
        retryDelayOnFailover: 100,
        connectTimeout: 5000,
        lazyConnect: true,
        enableAutoPipelining: false,
      }
    }
    
    if (connectionConfig) {
      // Create separate Redis instances for each BullMQ component
      bullmqQueueConnection = new Redis(connectionConfig)
      bullmqWorkerConnection = new Redis(connectionConfig)
      bullmqEventsConnection = new Redis(connectionConfig)
      
      // Add connection monitoring
      bullmqQueueConnection.on('connect', () => {
        console.log('BullMQ Queue Redis connected to database:', bullmqQueueConnection?.options.db)
      })
      
      bullmqWorkerConnection.on('connect', () => {
        console.log('BullMQ Worker Redis connected to database:', bullmqWorkerConnection?.options.db)
      })
      
      bullmqEventsConnection.on('connect', () => {
        console.log('BullMQ Events Redis connected to database:', bullmqEventsConnection?.options.db)
      })
      
      console.log('BullMQ Redis connections created with database 0')
    }
  } catch (error) {
    console.error('Failed to create BullMQ Redis connections:', error)
    bullmqQueueConnection = null
    bullmqWorkerConnection = null
    bullmqEventsConnection = null
  }
}

// Health check queue (only if BullMQ queue connection is available)
export const healthCheckQueue = bullmqQueueConnection ? new Queue('health-checks', {
  connection: bullmqQueueConnection,
  defaultJobOptions: {
    removeOnComplete: 10,
    removeOnFail: 5,
    attempts: 3,
    backoff: {
      type: 'exponential',
      delay: 2000,
    },
  },
}) : null

// Background jobs queue (only if BullMQ queue connection is available)
export const backgroundJobsQueue = bullmqQueueConnection ? new Queue('background-jobs', {
  connection: bullmqQueueConnection,
  defaultJobOptions: {
    removeOnComplete: 50,
    removeOnFail: 10,
    attempts: 2,
    backoff: {
      type: 'exponential',
      delay: 5000,
    },
  },
}) : null

// Queue events for monitoring (only if BullMQ events connection is available)
export const queueEvents = bullmqEventsConnection ? new QueueEvents('health-checks', { connection: bullmqEventsConnection }) : null

// Health check worker (only if BullMQ worker connection is available)
export const healthCheckWorker = bullmqWorkerConnection ? new Worker(
  'health-checks',
  async (job) => {
    const { type, data} = job.data
    
    switch (type) {
      case 'url-ping':
        return await performUrlPing(data)
      case 'inventory-validation':
        return await performInventoryValidation(data)
      case 'database-health':
        return await performDatabaseHealthCheck(data)
      case 'api-status':
        return await performApiStatusCheck(data)
      case 'health-scan':
        return await performHealthScan(data)
      default:
        throw new Error(`Unknown health check type: ${type}`)
    }
  },
  {
    connection: bullmqWorkerConnection,
    concurrency: 5,
  }
) : null

// Background jobs worker (only if BullMQ worker connection is available)
export const backgroundJobsWorker = bullmqWorkerConnection ? new Worker(
  'background-jobs',
  async (job) => {
    const { type, data } = job.data
    
    switch (type) {
      case 'sync-products':
        return await performProductSync(data)
      case 'ai-enrichment':
        return await performAIEnrichment(data)
      case 'cleanup-logs':
        return await performLogCleanup(data)
      case 'weekly-email-summary':
        return await performWeeklyEmailSummary(data)
      default:
        throw new Error(`Unknown background job type: ${type}`)
    }
  },
  {
    connection: bullmqWorkerConnection,
    concurrency: 3,
  }
) : null

// Health check functions
async function performUrlPing(data: { url: string; timeout?: number }) {
  try {
    const response = await fetch(data.url, {
      method: 'HEAD',
      signal: AbortSignal.timeout(data.timeout || 5000),
    })
    
    return {
      success: response.ok,
      status: response.status,
      responseTime: Date.now(),
      url: data.url,
    }
  } catch (error) {
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error',
      url: data.url,
    }
  }
}

async function performInventoryValidation(data: { shopId: string }) {
  try {
    // Check if user exists and has products
    const user = await db.user.findUnique({
      where: { shopId: data.shopId },
      include: {
        audits: {
          orderBy: { timestamp: 'desc' },
          take: 1,
        },
      },
    })

    if (!user) {
      return {
        success: false,
        error: 'User not found',
        shopId: data.shopId,
      }
    }

    const latestAudit = user.audits[0]
    const hasRecentSync = latestAudit && 
      (Date.now() - new Date(latestAudit.timestamp).getTime()) < 24 * 60 * 60 * 1000 // 24 hours

    return {
      success: true,
      hasRecentSync,
      totalProducts: latestAudit?.totalProducts || 0,
      lastSync: latestAudit?.timestamp || null,
      shopId: data.shopId,
    }
  } catch (error) {
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Database error',
      shopId: data.shopId,
    }
  }
}

async function performDatabaseHealthCheck(data: { shopId?: string }) {
  try {
    // Test database connectivity
    await db.$queryRaw`SELECT 1`
    
    // Check if we can read user data
    const userCount = await db.user.count()
    
    return {
      success: true,
      userCount,
      timestamp: new Date().toISOString(),
    }
  } catch (error) {
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Database connection failed',
    }
  }
}

