async function performLogCleanup(data: { daysToKeep?: number }) {
  try {
    const daysToKeep = data.daysToKeep || 30
    const cutoffDate = new Date()
    cutoffDate.setDate(cutoffDate.getDate() - daysToKeep)

    const deletedCount = await db.log.deleteMany({
      where: {
        createdAt: {
          lt: cutoffDate,
        },
      },
    })

    return {
      success: true,
      deletedCount: deletedCount.count,
      cutoffDate: cutoffDate.toISOString(),
    }
  } catch (error) {
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Log cleanup failed',
    }
  }
}

async function performHealthScan(data: { shopId: string; userId: string; options?: any }) {
  try {
    // Get user details for access token
    const user = await db.user.findUnique({
      where: { shopId: data.shopId }
    })

    if (!user) {
      throw new Error('User not found')
    }

    // Create health checker service
    const healthChecker = new HealthCheckerService(data.shopId, user.accessToken)
    
    // Perform comprehensive health scan
    const result = await healthChecker.performHealthCheck(data.options || {
      maxProducts: 100,
      includePings: true,
      includeInventory: true,
      includeValidation: true
    })

    // Log the scan
    await db.log.create({
      data: {
        userId: data.userId,
        type: 'health_scan',
        message: `Health scan completed: ${result.score}% score, ${result.gaps.length} gaps found`,
        metadata: {
          score: result.score,
          totalProducts: result.totalProducts,
          validProducts: result.validProducts,
          gapsCount: result.gaps.length
        }
      }
    })

    // Auto-fix if gaps > 10% and score < 90%
    if (result.score < 90 && result.gaps.length > 0) {
      const fixableGaps = result.gaps.filter(gap => gap.fixable)
      if (fixableGaps.length > 0) {
        const fixResult = await healthChecker.autoFixGaps(fixableGaps)
        
        await db.log.create({
          data: {
            userId: data.userId,
            type: 'auto_fix',
            message: `Auto-fixed ${fixResult.fixed} gaps, ${fixResult.failed} failed`,
            metadata: {
              fixed: fixResult.fixed,
              failed: fixResult.failed,
              originalScore: result.score
            }
          }
        })

        // Track auto-fix performance metrics
        const user = await db.user.findUnique({
          where: { id: data.userId }
        })

        if (user) {
          await analyticsService.trackPerformanceMetrics({
            userId: data.userId,
            shopDomain: data.shopId,
            timestamp: new Date(),
            healthScore: result.score,
            totalProducts: result.totalProducts,
            validProducts: result.validProducts,
            issuesFound: result.gaps.length,
            issuesFixed: fixResult.fixed,
            aiUsage: user.aiUsage,
            syncCount: 0,
            enrichmentCount: 0
          })
        }
      }
    }

    return {
      success: true,
      result,
      shopId: data.shopId,
      timestamp: new Date().toISOString(),
    }
  } catch (error) {
    await db.log.create({
      data: {
        userId: data.userId,
        type: 'error',
        message: `Health scan failed: ${error instanceof Error ? error.message : 'Unknown error'}`,
        error: error instanceof Error ? error.message : 'Unknown error'
      }
    })

    return {
      success: false,
      error: error instanceof Error ? error.message : 'Health scan failed',
      shopId: data.shopId,
    }
  }
}

// Schedule recurring health checks
export async function scheduleHealthChecks() {
  if (!healthCheckQueue || !backgroundJobsQueue) {
    console.log('Health checks skipped - queues not available')
    return
  }

  try {
    // Database health check every 5 minutes
    await healthCheckQueue.add(
      'database-health',
      {},
      {
        repeat: { pattern: '*/5 * * * *' },
        jobId: 'database-health-recurring',
      }
    )

    // URL ping every 2 minutes
    await healthCheckQueue.add(
      'url-ping',
      { url: process.env.SHOPIFY_APP_URL + '/health' },
      {
        repeat: { pattern: '*/2 * * * *' },
        jobId: 'url-ping-recurring',
      }
    )

    // Log cleanup daily at 2 AM
    await backgroundJobsQueue.add(
      'cleanup-logs',
      { daysToKeep: 30 },
      {
        repeat: { pattern: '0 2 * * *' },
        jobId: 'log-cleanup-recurring',
      }
    )

    // Weekly email summaries every Monday at 8 AM
    await backgroundJobsQueue.add(
      'weekly-email-summary',
      {},
      {
        repeat: { pattern: '0 8 * * 1' },
        jobId: 'weekly-email-summary-recurring',
      }
    )
    
    console.log('Health checks scheduled successfully')
  } catch (error) {
    console.error('Failed to schedule health checks:', error)
  }
}

async function performWeeklyEmailSummary(data: { shopId?: string; userId?: string }) {
  try {
    // Get all users or specific user
    const users = data.userId ? 
      [await db.user.findUnique({ where: { id: data.userId } })] :
      await db.user.findMany()

    const validUsers = users.filter((user: any) => user !== null)

    for (const user of validUsers) {
      if (!user) continue

      // Create health checker service
      const healthChecker = new HealthCheckerService(user.shopDomain, user.accessToken)
      
      // Send weekly health summary
      const success = await healthChecker.sendWeeklyHealthSummary(user.id)

      await db.log.create({
        data: {
          userId: user.id,
          type: 'weekly_email_summary',
          message: `Weekly email summary ${success ? 'sent' : 'failed'} for ${user.shopDomain}`,
          metadata: {
            success,
            shopDomain: user.shopDomain
          }
        }
      })
    }

    return {
      success: true,
      usersProcessed: validUsers.length,
      timestamp: new Date().toISOString(),
    }
  } catch (error) {
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Weekly email summary failed',
    }
  }
}

// Schedule daily health scans for all users
export async function scheduleDailyHealthScans() {
  if (!healthCheckQueue) {
    console.log('Health scans skipped - queue not available')
    return
  }

  try {
    // Get all users
    const users = await db.user.findMany({
      select: {
        id: true,
        shopId: true,
        tier: true
      }
    })

    for (const user of users) {
      // Schedule daily health scan at 2 AM UTC
      await healthCheckQueue.add(
        'health-scan',
        {
          shopId: user.shopId,
          userId: user.id,
          options: {
            maxProducts: user.tier === 'enterprise' ? 500 : 100,
            includePings: true,
            includeInventory: true,
            includeValidation: true
          }
        },
        {
          repeat: { pattern: '0 2 * * *' },
          jobId: `health-scan-${user.shopId}`,
        }
      )
    }
    
    console.log(`Daily health scans scheduled for ${users.length} users`)
  } catch (error) {
    console.error('Failed to schedule daily health scans:', error)
  }
}

// Graceful shutdown
export async function shutdownQueues() {
  const promises = []
  
  if (healthCheckWorker) promises.push(healthCheckWorker.close())
  if (backgroundJobsWorker) promises.push(backgroundJobsWorker.close())
  if (queueEvents) promises.push(queueEvents.close())
  if (bullmqQueueConnection) promises.push(bullmqQueueConnection.disconnect())
  if (bullmqWorkerConnection) promises.push(bullmqWorkerConnection.disconnect())
  if (bullmqEventsConnection) promises.push(bullmqEventsConnection.disconnect())
  if (redis) promises.push(redis.disconnect())
  
  await Promise.all(promises)
}

// Error handling (only if workers exist)
if (healthCheckWorker) {
  healthCheckWorker.on('error', (error) => {
    console.error('Health check worker error:', error)
  })
}

if (backgroundJobsWorker) {
  backgroundJobsWorker.on('error', (error) => {
    console.error('Background jobs worker error:', error)
  })
}

if (queueEvents) {
  queueEvents.on('error', (error) => {
    console.error('Queue events error:', error)
  })
}
